	export default {
		data() {
			return {
				menu: [],
				// display: 'none',
				message: ''
			};
		},
		onLoad() {
			let _this = this;
			_this.submitAjax("appmodule/appFile/selectLendFiles", "", "get", function(res) {
				if (res.data.state) {
					_this.menu = res.data.rows;
				} else {
					// _this.display = '';
					_this.message = res.data.message;
				}
			})
		},
		methods: {
			lend: function(id, name) {
				/* let _this=this; */
				/* _this.submitAjax("appmodule/appFile/lend",{ids:id},"post",function(res){
					console.log(res.data);
					if(res.data.state){
						
					}
				}) */
				uni.setStorage({
					key: "file",
					data: {
						id: id,
						name: name
					}
				})
				uni.navigateTo({
					url: 'borrowProcess'
				})
			}
		}
	}
